import React,{useState} from 'react';
import ThumbUpOffAltIcon from '@mui/icons-material/ThumbUpOffAlt';
import ThumbDownOffAltIcon from '@mui/icons-material/ThumbDownOffAlt';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';

const MovieRowCard = ({setIdTime,idTime,clearTime,positionTrailer, clearData, trailerMovie, op, setOp,aparece, setAparece, dataTrailerMovie}) => {

	const handleActive = ()=>{
		aparece.h === 0 ? setAparece({h:'auto',p:20}) : setAparece({h:0,p:0})
		if(op.w === '560px'){
			setOp({w:'1000px',h:'auto',h2:'500px'})
		}else{
			setOp({w:'560px',h:'auto'})
		}
	}


	const activeTime = ()=>{
		const time = window.setTimeout(()=>clearData(),800)
		setIdTime(time)
	}


	return (
		<div className="movieRow--item--trailer" style={{left:positionTrailer ? `${positionTrailer.screenX - 230}px` : 0, width:op.w, height:op.h}} onMouseEnter={e=>clearTime(idTime)} onMouseLeave={e=>activeTime()}>
			{
				trailerMovie ?
				<iframe width={op.w} style={{minHeight:'315px', height:op.h2 ? op.h2 : op.h}} src={trailerMovie} title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" ></iframe>
				:
				<img src="https://cdn-icons-png.flaticon.com/512/732/732228.png" alt="" style={{height:'315px',width: op.w,}}/>

			}
			<div className="movieRow--item--trailer--data--button">
				<div>
					<button className="trailer--data--button1" >
						<ThumbUpOffAltIcon style={{fontSize:'30px'}}/>
					</button>
					<button className="trailer--data--button2" >
						<ThumbDownOffAltIcon style={{fontSize:'30px'}}/>
					</button>
				</div>
				<div>
					<button className="trailer--data--button3" onClick={e=> handleActive()}>
						<KeyboardArrowDownIcon style={{fontSize:'30px'}}/>
					</button>
				</div>

			</div>
			
			<div className="movieRow--item--trailer--data" style={{height:`${aparece.h}`,paddingTop:aparece.p,paddingBottom:aparece.p}}>

				<div className="trailer--data">
					<h2>{dataTrailerMovie?.name ? dataTrailerMovie?.name : dataTrailerMovie?.title }</h2>
					<div className="trailer">
						<p className="trailer--vote">{dataTrailerMovie?.vote_average} Puntos</p>
						<p className="trailer--year">{dataTrailerMovie?.year} </p>
						<p className="trailer--temps">{dataTrailerMovie?.number_of_seasons} {dataTrailerMovie?.number_of_seasons > 1 ? 'Temporadas':'Temporada'}</p>
					</div>
					<div className="trailer--overview">{dataTrailerMovie?.overview}</div>
				</div>
				<div className="trailer--extra">
					<div>
						Generos: <strong>{dataTrailerMovie?.genre.join(', ')}</strong>
					</div>
					<div>
						Creado por: <strong>{dataTrailerMovie?.create?.join(', ')} </strong>
					</div>
				</div>
			</div>
			
		</div>
		
	)
}


export default MovieRowCard